.seccion {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.contenido-centrado {
  max-width: 800px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: "Fredoka", sans-serif;
}

.container {
  width: 100%;
}

.navigation {
  position: fixed;
  width: 250px;
  height: 100%;
  max-width: 100%;
  background-color: #b17c00;
  border-left: 10px solid #b17c00;
  transition: 0.5s;
  overflow: hidden;
}

.navigation.menu-active {
  width: 70px;
}

.navigation ul {
  position: absolute;
  left: 0;
  top: 0;
  height: 50%;
  width: 100%;
  margin-top: 4rem;
  /* display: flex;
  flex-direction: column;
  justify-content: space-between; */
}

.navigation ul li {
  position: relative;
  width: 100%;
  list-style: none;
  border-top-left-radius: 30px;
}

.navigation ul li:nth-child(1) {
  margin-bottom: 30px;
  font-size: 3rem;
}

.navigation ul li.hovered,
.navigation ul li:hover {
  background-color: #0c0f0a;
  cursor: pointer;
}

.navigation ul li a {
  position: relative;
  display: block;
  width: 100%;
  display: flex;
  text-decoration: none;
  color: #fff;
  font-size: 2rem;
}

.navigation ul li.hovered a,
.navigation ul li a:hover {
  color: #b17c00;
}

.navigation ul li a .icon {
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  line-height: 65px;
  text-align: center;
}

.navigation ul li a .icon i {
  font-size: 2rem;
}

.navigation ul li a .title {
  position: relative;
  display: block;
  padding: 0 5px;
  height: 60px;
  line-height: 60px;
  text-align: start;
  white-space: nowrap;
}

/*Curva de fuera del li*/
.navigation ul li.hovered a::before,
.navigation ul li:hover a::before {
  content: "";
  position: absolute;
  right: 0;
  width: 50px;
  height: 50px;
  background-color: transparent;
  top: -50px;
  border-radius: 50%;
  box-shadow: 35px 35px 0 10px #0c0f0a;
}

/*Main*/
.main {
  position: absolute;
  left: 250px;
  width: calc(100% - 250px);
  min-height: 100vh;
  background-color: #0c0f0a;
  transition: 0.5s;
}

.main.menu-active {
  width: calc(100% - 70px);
  left: 70px;
}

.main .header {
  color: #b17c00;
  display: flex;
  justify-content: space-between;
}

.main .header h1 {
  text-align: center;
  font-size: 4rem;
  margin: 0;
}

.main .header .toggle {
  width: 60px;
  height: 60px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
  cursor: pointer;
}

.main .header .social-media {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}

.main .header .social-media a {
  color: #b17c00;
  font-size: 5rem;
}

/*Diseño responsivo*/
@media (max-width: 991px) {
  .navigation {
    left: -250px;
    overflow: scroll;
  }

  .navigation.menu-active {
    width: 250px;
    left: 0;
  }

  .main {
    width: 100%;
    left: 0;
  }

  .main.menu-active {
    left: 250px;
  }
}
@media (max-width: 480px) {
  .navigation {
    width: 100%;
    left: -100%;
    z-index: 1000;
  }

  .navigation.menu-active {
    width: 100%;
    left: 0;
  }

  .toggle {
    z-index: 10001;
  }

  .main.menu-active .toggle {
    position: fixed;
    color: #fff;
    right: 0;
    /* position:static;/*Si le quito esto funciona pero no funciona*/
  }

  .main .header {
    margin-top: 0;
    flex-direction: column;
    align-items: center;
  }
}
.stage,
.ring,
.img {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  user-select: none;
}

.stage {
  background: #000;
}

.carrousel div {
  position: absolute;
}

.container-img {
  perspective: 2000px;
  width: 300px;
  height: 400px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.nosotros {
  display: grid;
}
.nosotros .nosotros-img {
  grid-row: 1;
}
@media (min-width: 768px) {
  .nosotros {
    grid-template-columns: 2fr 1fr;
    padding: 1rem 2rem;
    column-gap: 2rem;
  }
  .nosotros .nosotros-img {
    grid-column: 2;
  }
}
.nosotros .nosotros-info {
  color: #fef9ef;
  font-size: 2rem;
}

.jefes {
  margin-top: 5rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 4rem;
  justify-content: center;
  max-width: 100%;
}

.jefe {
  width: 40rem;
  padding: 1rem;
  box-shadow: 3px 3px 15px 5px #16c172;
  color: #fff;
}
.jefe .jefe-name span {
  font-size: 3rem;
  font-weight: bold;
}
.jefe .jefe-name h3 {
  margin: 0;
  font-size: 2.5rem;
}
.jefe .jefe-info p {
  color: #fef9ef;
}

.main-index {
  padding: 0 2rem;
  margin-top: 10rem;
}

.form {
  padding: 0 5rem;
  margin-top: 5rem;
}

p {
  font-size: 1.4rem;
  color: #fef9ef;
  margin: 2rem 0;
}

label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  color: #fef9ef;
  font-size: 2.5rem;
}

input:not([type=submit]), textarea, select {
  padding: 1rem;
  display: block;
  background-color: #fef9ef;
  width: 70%;
  border: 1px solid #fef9ef;
  border-radius: 5px;
  margin-bottom: 2rem;
  outline: none;
  font-size: 1.5rem;
}

.btn-verde {
  background-color: #16c172;
  color: #fef9ef;
  font-size: 2rem;
  border-radius: 10px;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: block;
  outline: none;
  border: none;
  cursor: pointer;
}
.btn-verde:hover {
  background-color: #13aa64;
}