@use 'base/variables' as v;
@use 'base/mixins' as m; 

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 62.5%;
    font-family: v.$fuente;
    
}


.container{
    
    width: 100%;
}

.navigation{
    position: fixed;
    width: 250px;
    height: 100%;
    max-width: 100%;
    background-color: v.$amarillo;
    border-left: 10px solid v.$amarillo;
    transition: 0.5s;
    overflow: hidden;
}
.navigation.menu-active{
    width: 70px;
}

.navigation ul{
    position: absolute;
    left: 0;
    top: 0;
    height: 50%;
    width: 100%;
    margin-top: 4rem;
    /* display: flex;
    flex-direction: column;
    justify-content: space-between; */
}

.navigation ul li{
    position: relative;
    width: 100%;
    list-style: none;
    border-top-left-radius: 30px;
}

.navigation ul li:nth-child(1){
    margin-bottom: 30px;

    font-size: 3rem;
    
}
.navigation ul li.hovered,
.navigation ul li:hover{
    background-color: v.$oscuro;
    cursor: pointer;
    
}
.navigation ul li a{
    position: relative;
    display: block;
    width: 100%;
    display: flex;
    text-decoration: none;
    color: v.$blanco;
    font-size: 2rem;
}

.navigation ul li.hovered a,
.navigation ul li a:hover{
    color: v.$amarillo;
}

.navigation ul li a .icon{
    position: relative;
    display: block;
    min-width: 60px;
    height: 60px;
    line-height: 65px;
    text-align: center;
}

.navigation ul li a .icon i{
    font-size: 2rem;
}

.navigation ul li a .title{
    position: relative;
    display: block;
    padding: 0 5px;
    height: 60px;
    line-height: 60px;
    text-align: start;
    white-space: nowrap;
}

/*Curva de fuera del li*/
.navigation ul li.hovered a::before,
.navigation ul li:hover a::before{
    content: '';
    position: absolute;
    right: 0;
    width: 50px;
    height: 50px;
    background-color: transparent;
    top: -50px;
    border-radius: 50%;
    box-shadow: 35px 35px 0 10px v.$oscuro;
}


/*Main*/
.main{
    position: absolute;
    left: 250px;
    width: calc(100% - 250px);
    min-height: 100vh;
    background-color: v.$oscuro;
    transition: 0.5s;
}
.main.menu-active{
    width: calc(100% - 70px);
    left: 70px;
    
}

.main .header{
    color: v.$amarillo;
    display: flex;
    justify-content: space-between;
}

.main .header h1{
    text-align: center;
    font-size: 4rem;
    margin: 0;
}


.main .header .toggle{
    width: 60px;
    height: 60px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 5rem;
    cursor: pointer;
}
.main .header .social-media{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    gap: 2rem;
}

.main .header .social-media a{
    color: v.$amarillo;
    font-size: 5rem;
}

/*Diseño responsivo*/

@media (max-width: 991px){
    .navigation{
        left: -250px;
        overflow: scroll;

    }

    .navigation.menu-active{
        width: 250px;
        left: 0;
    }

    .main{
        width: 100%;
        left: 0;
    }
    .main.menu-active{
        left: 250px;
    }
}

@media (max-width: 480px){
    .navigation{
        width: 100%;
        left: -100%;
        z-index: 1000;
    }

    .navigation.menu-active{
        width: 100%;
        left: 0;
    }

    .toggle{
        z-index: 10001; 
        
    }
    .main.menu-active .toggle{
        position: fixed;
        color: v.$blanco;
        right: 0;
       /* position:static;/*Si le quito esto funciona pero no funciona*/
    }

    .main .header{
        margin-top: 0;
        flex-direction: column;
        align-items: center;
    }
}

