@use 'base/variables' as v;
@use 'base/mixins' as m; 

.form{
    padding: 0 5rem;
    margin-top: 5rem;
    
}

p{
    font-size: 1.4rem;
    color: v.$crema;
    margin: 2rem 0;
}


label{
    font-weight: v.$bold;
    text-transform: uppercase;
    display: block;
    color: v.$crema;
    font-size: 2.5rem;
    
}

input:not([type="submit"]), textarea, select{
    
    padding: 1rem;
    display: block;
    background-color: v.$crema;
    width: 70%;
    border: 1px solid v.$crema;
    border-radius: 5px;
    margin-bottom: 2rem;
    outline: none;
    font-size: 1.5rem;
}

.btn-verde{
    background-color: v.$verde;
    color: v.$crema;
    font-size: 2rem;
    border-radius: 10px;
    font-weight: v.$bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: 2rem;
    display: block;
    outline: none;
    border: none;
    cursor: pointer;
    &:hover{
        background-color: darken(v.$verde, 5);
    }
}






