@use 'base/variables' as v;
@use 'base/mixins' as m; 


.nosotros{
    display: grid;
    .nosotros-img{
        grid-row: 1;
    }
    @include m.tablet {
        
        grid-template-columns: 2fr 1fr;
        padding: 1rem 2rem;
        column-gap: 2rem;

        .nosotros-img{
            grid-column: 2;
        }   
    }

    .nosotros-info{
        color: v.$crema;
        font-size: 2rem;

        
    }
}

