@use 'base/variables' as v;
@use 'base/mixins' as m; 

.jefes{
    margin-top: 5rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 4rem;
    justify-content: center;
    max-width: 100%;

    
}

.jefe{
    width: 40rem;
    padding: 1rem;
    box-shadow: 3px 3px 15px 5px v.$verde;
    color: v.$blanco;
    .jefe-name{
        
        span{
            font-size: 3rem;
            font-weight: bold;
        }

        h3{
            margin: 0;
            font-size: 2.5rem;
        }
    }

    .jefe-info{
        p{
            color: v.$crema;
        }
        
    }

}