@use "base/variables" as v;
@use "base/mixins" as m;

.stage,
.ring,
.img {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  user-select: none;
}

.stage {
  background: #000;
}

.carrousel div {

    position: absolute;
  
}

.container-img {
  perspective: 2000px;
  width: 300px;
  height: 400px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

