@use 'base/variables' as v;
@use 'base/mixins' as m; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: v.$fuente;
    font-size: 1.6rem;
    line-height: 1.8;
}
p {   
    color: v.$blanco;
    font-size: 2rem;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: v.$fuente;
    margin: v.$separacion 0;
    font-weight: 300;
    text-align: center;
    
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}